import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup"
import { TextField } from "@material-ui/core";
import { useHistory, Redirect } from "react-router-dom";
import axios from "axios";

export const FormLogin = ({ autenticado, setAutenticado }) => {

    const history = useHistory()

    const formSchema = yup.object().shape({
        email: yup.string().email().required('Campo obrigatório'),
        password: yup.string().required('Campo obrigatório'),
    })

    const { register, handleSubmit, formState: { errors }} = useForm({
        resolver: yupResolver(formSchema)
    })

    const onSubmitFunc = (data) => {
            
        axios.post("https://kenziehub.me/sessions", data)
        .then(res => {
            const { token } = res.data

            localStorage.setItem("@kenziehub:token", JSON.stringify(token))

            setAutenticado(true)

            return history.push("/dashboard")
        })
        .catch(err => console.log('Email ou senha inválidos' + err))
        console.log(data)
    }

    if(autenticado) {
        return <Redirect to={"/dashboard"} />
    }   

    return (
        <>
        <h1>Login</h1>
        <form onSubmit={handleSubmit(onSubmitFunc)}>
         <div>
            <TextField 
                label="E-mail" 
                variant="outlined"
                size="small" 
                {...register("email")}
                />
                <h5>{errors.email && errors.email.message}</h5>
        </div>

        <br />
        
        <div>
            <TextField 
                label="Senha" 
                variant="outlined"
                size="small" 
                {...register("password")}
            />
             <h5>{errors.password && errors.password.message}</h5>
        </div>

        <br />
        <div>
            <button type="submit">Entrar</button>
        </div>
     </form>
  </>
    )
}