import { useState, useEffect } from "react"
import axios from "axios"

export const Dashboard = () => {

    const [alunos, setAlunos] = useState([])

    useEffect(() => {
        axios.get("https://kenziehub.me/users")
        .then(res => setAlunos(res.data))
    }, [])

    return (
        <div>
            {alunos.map(alu => {
                return (
                    <div key={alu.id}>
                        <h2>{alu.name}</h2>
                    </div>
                )
            })}
        </div>
    )
}