import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup"
import { TextField } from "@material-ui/core";
import axios from "axios"
import { useHistory, Redirect } from "react-router-dom";

export const FormRegister = ({ autenticado }) => {

    const history = useHistory()

    const formSchema = yup.object().shape({
        name: yup.string().required('Campo obrigatório'),
        email: yup.string().email().required('Campo obrigatório'),
        password: yup.string().min(8, 'min 8 digitos').required("Campo obrigatório"),
        bio: yup.string().required('Campo obrigatório'),
        contact: yup.string().required('Campo obrigatório'),
        course_module: yup.string().required('Campo obrigatório')
    })

    const { register, handleSubmit, formState: { errors }} = useForm({
        resolver: yupResolver(formSchema)
    })

    const onSubmitFunc = (data) => {

        console.log(data)
        axios.post("https://kenziehub.me/users", data)
        .then(res => {
            alert('Usúario cadastrado')
            return history.push("/login")
        })
        .catch(err => console.log('Erro ao se cadastrar + ' + err))
    }

    if(autenticado) {
        return <Redirect to={"/dashboard"}/> 
    }

    return (
        <>
        <h1>Cadastro</h1>
        <form onSubmit={handleSubmit(onSubmitFunc)}>

        <div>
            <TextField 
                label="E-mail" 
                variant="outlined"
                size="small" 
                {...register("email")}
            />
                <h5>{errors.email && errors.email.message}</h5>
        </div>
        
        <div>
            <TextField 
                label="Senha" 
                variant="outlined"
                size="small" 
                {...register("password")}
            />
             <h5>{errors.password && errors.password.message}</h5>
        </div>
        
         <div>
            <TextField 
                label="Nome" 
                variant="outlined"
                size="small" 
                {...register("name")}
                />
                <h5>{errors.name && errors.name.message}</h5>
        </div>
        <div>
            <TextField 
                label="Bio" 
                variant="outlined"
                size="small" 
                {...register("bio")}
            />
            <h5>{errors.confirmPassword && errors.confirmPassword.message}</h5>
        </div>

        <div>
            <TextField 
                label="contact" 
                variant="outlined"
                size="small" 
                {...register("contact")}
            />
            <h5>{errors.contact && errors.contact.message}</h5>
        </div>

        <div>
            <TextField 
                label="course_module" 
                variant="outlined"
                size="small" 
                {...register("course_module")}
            />
            <h5>{errors.course_module && errors.course_module.message}</h5>
        </div>
        <br />
        <div>
            <button type="submit">Cadastrar</button>
        </div>
     </form>
  </>
    )
}